<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <div class="ucGreenTit">我的收藏</div>
                        <div class="cList" v-if="list.length!=0">
                            <el-row :gutter="12">
                                <el-col :span="6" v-for="(item, index) in list" :key="index" class="collectCard mb10">
                                    <el-card class="cCard" shadow="hover">
                                        <el-image @click="toDetail(item.spec_id)" class="cImg" :src="item.list_pic_url" fit="cover"></el-image>
                                        <div>
                                            <h2 class="w wto" @click="toDetail(item.spec_id)">{{item.good_name}}</h2>
                                            <div class="color9">规格：{{item.spec_name}}{{item.spec_unit}}</div>
                                            <div class="cbottom">
                                                <div class="colorPrice price">
                                                    {{item.price_type}}{{item.price_cn}}<span class="f14">/{{item.supply_unit}}</span>
                                                </div>
                                                <el-link  @click="delCollect(item.collect_id)" icon="el-icon-delete" type="info">删除</el-link>
                                            </div>
                                        </div>
                                    </el-card>
                                </el-col>
                            </el-row>
                            <el-pagination
                                    class="tc mt15"
                                    @current-change="handleCurrentChange"
                                    hide-on-single-page
                                    background
                                    layout="prev, pager, next"
                                    :current-page="page"
                                    :page-size="rows"
                                    :total="count">
                            </el-pagination>
                        </div>
                        <div v-else class="f16 color6 tc mt30">暂无数据</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    import {mapState} from 'vuex'
    import {getMyCollection,delMyCollection} from '../../../service/api'
    export default {
        name: 'collect',
        data(){
            return{
                list:[],
                rows:8,//每页显示数
                page:1,//当前页面
                count:0//总条数
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.initData();

        },
        computed:{
            ...mapState([
                'login'
            ])
        },
        methods:{
            async initData(){
                var res = await getMyCollection(
                    this.rows,
                    this.page
                );
                if(res){
                    if(res.status != 12007){
                        this.list = res.data;
                        this.count = res.count;

                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
            },
            async delCollect(collect_id){
                this.$confirm('你确定要取消该收藏吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await delMyCollection(collect_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });
            },
            toDetail(specifications_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:specifications_id}});
            },
            handleCurrentChange(val) {
                //改变页码
                this.page = val;
                this.initData();
            }

        },
        created(){

        },

    }

</script>

<style scoped>
    /*我的收藏*/
    .collectCard{
        cursor:pointer;
    }
    .bottom {
        margin-top: 13px;
        line-height: 12px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    .button {
        padding: 0;
        float: right;
    }
    .cCard.el-card__body{
        padding:0 ;
    }

    .cImg{
        width: 100%;
        height:180px;
        display: block;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }
    .price{
        font-size:16px;
    }
    .cList{
        min-height: 400px;
        margin-bottom:15px;
        font-size: 14px;
    }
    .cbottom{
        margin-top: 0;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }
    .address-container{
        height: 100%;
    }

</style>



